import style from './CompanyData.module.css'


const CompanyData = (props)=>{

 
    return(
        <div className={style.companyDataBox}>
           <h3>   </h3>
           <div>
                <p>: {props.value}</p>
                <p>: {props.inn}</p>
                <p>: {props.address}</p>
                <p>: {props.general}</p>
                <p>: {props.status}</p>
           </div>
        </div>
    )
}

export default CompanyData